import {
  Component,
  ElementRef,
  HostBinding,
  TemplateRef,
  ViewChild
} from '@angular/core';

@Component({
  selector: 'gda-menu, .gda-menu, [gda-menu]',
  template: `
    <ng-template #content>
      <ng-content></ng-content>
    </ng-template>
  `
})
export class GdaMenu {
  /**
   * Display
   */
  @HostBinding('style.display') private setStyle = 'none';

  @ViewChild('content', { read: TemplateRef, static: false }) public contentTemplate!: TemplateRef<any>;

  constructor(public el: ElementRef) { }
}
